import VueRouter from 'vue-router'
import Home from '@/component/Home'
import Other from '@/component/Other'
const routes = [{
    path: '/home',
    component: Home
}, {
	path: '/other',
    component: Other
},{
    path: '/*',
    redirect: '/home'
}];
export default new VueRouter({
	mode:'hash',
	routes
})